import React, { useContext } from 'react';
import { LoginContext } from '../../../pages/Login/Login';
import ContentP from './ContentP';
import './Paragraph.css';

const Paragraph = () => {
	const value = useContext(LoginContext);

	return (
		<div className={value.isLogin ? 'paragraph active' : 'paragraph'}>
			{value.isLogin ? (
				<ContentP
					title="WELLCOME !"
					text="Xin cho bn  n vi Mystery, chng ti s cung cp cho bn nhng mu sn phm tt nht"
					suggest="Nu cha c ti khong"
					button="ng k"
				/>
			) : (
				<ContentP
					title="HEY, YOU !"
					text="Cho bn  quay tr li Mystery, cm n bn  la chn sn phm ca chng ti"
					suggest="Nu  ti khong"
					button="ng nhp"
				/>
			)}
		</div>
	);
};

export default Paragraph;
